@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Gravitas+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Covered+By+Your+Grace&display=swap);
body {
  font-family: 'Roboto', sans-serif; }

.header-font, .footer-font {
  font-weight: 300;
  font-size: 16px;
  letter-spacing: 0.05em;
  text-align: center;
  color: #42191c;
  margin: 0px; }

.layout {
  position: relative;
  min-height: 100vh; }

.content {
  background: #fff5d7;
  padding-bottom: 6rem; }

.navbar-responsive {
  background: #fff5d7; }
  .navbar-responsive .ant-menu {
    border-bottom: 0px;
    background: rgba(0, 0, 0, 0); }
    .navbar-responsive .ant-menu .ant-menu-item-active,
    .navbar-responsive .ant-menu .ant-menu-item-selected {
      border-color: #42191c; }

.banner-container {
  width: 100%;
  height: 100%;
  background: #fff5d7;
  padding: 1vh 15vw 1vh 20vw; }
  .banner-container .ant-row {
    height: 100%; }
    .banner-container .ant-row .ant-col {
      height: 100%; }
      .banner-container .ant-row .ant-col .image-container,
      .banner-container .ant-row .ant-col .banner-description-container {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center; }
      .banner-container .ant-row .ant-col .image-container {
        display: flex;
        align-items: center;
        padding-bottom: 50%; }
        .banner-container .ant-row .ant-col .image-container .portrait-image {
          position: absolute;
          width: 100%;
          top: 50%;
          left: 50%;
          -webkit-transform: translate(-50%, -50%);
                  transform: translate(-50%, -50%);
          margin: 0;
          border-radius: 50%;
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); }
      .banner-container .ant-row .ant-col .banner-description-container {
        display: flex;
        flex-direction: column; }
        .banner-container .ant-row .ant-col .banner-description-container h1,
        .banner-container .ant-row .ant-col .banner-description-container h2 {
          color: #42191c; }
        .banner-container .ant-row .ant-col .banner-description-container h1 {
          font-family: 'Gravitas One', cursive;
          font-size: 72px;
          margin-bottom: 0px; }
        .banner-container .ant-row .ant-col .banner-description-container h2 {
          font-size: 48px;
          font-weight: lighter; }

.header {
  display: flex;
  justify-content: space-between;
  padding-left: 25px;
  padding-right: 25px;
  background: #fff5d7; }

@-webkit-keyframes fadeIn {
  0% {
    color: #fff5d7; }
  100% {
    color: #42191c; } }

@keyframes fadeIn {
  0% {
    color: #fff5d7; }
  100% {
    color: #42191c; } }

@-webkit-keyframes fadeOut {
  0% {
    color: #fff5d7; }
  100% {
    color: #42191c; } }

@keyframes fadeOut {
  0% {
    color: #fff5d7; }
  100% {
    color: #42191c; } }
  .header .name {
    -webkit-animation: fadeOut 0.75s;
            animation: fadeOut 0.75s; }
    .header .name:hover {
      -webkit-animation: fadeIn 0.75s;
              animation: fadeIn 0.75s; }
  .header .navbar {
    display: flex;
    align-items: center; }
    .header .navbar .ant-menu {
      border-bottom: 0px;
      background: rgba(0, 0, 0, 0); }
      .header .navbar .ant-menu .ant-menu-item-active,
      .header .navbar .ant-menu .ant-menu-item-selected {
        color: #ff5e6c;
        border-color: #ff5e6c; }
    .header .navbar li {
      margin-left: 25px; }
  .header #sider-toggle-button {
    color: #42191c; }
    .header #sider-toggle-button svg {
      width: 32px;
      height: 32px; }

.breakpoint__small-down {
  display: flex;
  justify-content: center;
  align-items: center; }

.breakpoint__medium-up {
  display: flex; }

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: center;
  background: #fff5d7;
  padding-top: 0px;
  padding-bottom: 0px; }
  .footer .footer-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 50%; }
    .footer .footer-container .footer-buttons {
      display: flex;
      justify-content: center; }
      .footer .footer-container .footer-buttons a,
      .footer .footer-container .footer-buttons button {
        margin: 0px 5px 0px 5px;
        color: #42191c; }
        .footer .footer-container .footer-buttons a:hover,
        .footer .footer-container .footer-buttons button:hover {
          color: #ff5e6c; }

.about-page-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }
  .about-page-container h1 {
    font-family: 'Gravitas One', cursive;
    font-size: 72px;
    color: #42191c; }
  .about-page-container svg,
  .about-page-container ant-timeline-item-head {
    color: #ff5e6c;
    border-color: #ff5e6c; }

.resume-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-bottom: 2rem; }
  .resume-container .react-pdf__Document {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); }
  .resume-container #resume-download-button {
    margin: 20px 0px 20px 0px;
    color: #42191c; }
    .resume-container #resume-download-button:hover {
      color: #ff5e6c; }
    .resume-container #resume-download-button svg {
      height: 32px;
      width: 32px; }

.projects-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }
  .projects-container h1 {
    font-family: 'Gravitas One', cursive;
    font-size: 72px;
    color: #42191c; }
  .projects-container .ant-row {
    height: 80vh;
    width: 100%; }
    .projects-container .ant-row .ant-col {
      height: 100%;
      display: flex;
      justify-content: center; }
  .projects-container .projects-card {
    height: 100%;
    width: 100%; }
    .projects-container .projects-card .ant-card-cover {
      height: 75%; }
      .projects-container .projects-card .ant-card-cover .card-cover {
        height: 100%;
        background-size: cover;
        background-position: center; }

